import { Edit } from "@mui/icons-material";
import { Box, IconButton, Rating } from "@mui/material";
import { Fragment, useState } from "react";
import Delete from "icons/Delete";
import CreatePlanCuentaModal from "./create-plan-cuenta";
import usePlanCuenta from '../../hooks/plan-cuentaContext'
const PlanCuentaColumns = [
    {
        Header: "Codigo",
        accessor: "codigo"
    },
    {
        Header: "Nombre",
        accessor: "nombreCuenta"
    },
    {
        Header: "Moneda",
        accessor: "moneda"
    },
    {
        Header: "Valor",
        accessor: "valor"
    },
    {
        Header: "Nivel",
        accessor: "nivel"
    },
    {
        Header: "Debe",
        accessor: "debe"
    },
    {
        Header: "Haber",
        accessor: "haber"
    },
    {
        Header: "Acciones",
        accessor: "acciones",
        Cell: ({
            row
        }) => {
            const style = {
                fontSize: 19,
                transition: "color 0.3s",
                color: row.isSelected ? "white" : "text.disabled"
            };

            const { codigo, modal } = usePlanCuenta();

            const [openModal, setOpenModal] = useState(false);
            return <Fragment>
                <IconButton onClick={() => setOpenModal(true)}>
                    <Edit sx={{
                        fontSize: 18,
                        color: "text.disabled"
                    }} />
                </IconButton>
                <CreatePlanCuentaModal editProduct open={openModal} data={row.original} onClose={() => { console.log(codigo, modal); return setOpenModal(false) }} />
                <IconButton onClick={() => { alert('funcion pendiente') }}>
                    <Delete sx={style} />
                </IconButton>
            </Fragment>;
        }
    }];
export default PlanCuentaColumns;