import { Add } from "@mui/icons-material";
import { Box, Button, styled } from "@mui/material";
import FlexBox from "components/flexbox/FlexBox";
import IconWrapper from "components/IconWrapper";
import SearchInput from "components/input-fields/SearchInput";
import ShoppingBasket from "icons/ShoppingBasket";
import CustomTable from "page-sections/admin-ecommerce/CustomTable";
import { H5 } from "components/Typography";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { searchByName } from "./components/plan-cuenta-util";
import PlanCuentaColumns from "./components/plan-cuenta-columns";
import { CrearPlanCuentaService, ObtenerPlanCuentaService, } from "Services/api-ventas-erp/proveedores";
import PlancuentaContext from '../context/PlanCuentasContext'
import { plancuentaFake } from "./components/plan-cuenta-fake";
import CreatePlanCuentaModal from "./components/create-plan-cuenta";

export const HeadingWrapper = styled(FlexBox)(({
  theme
}) => ({
  marginBottom: 20,
  flexWrap: "wrap",
  [theme.breakpoints.down(530)]: {
    "& .MuiButton-root": {
      width: "100%"
    },
    "& .MuiInputBase-root": {
      maxWidth: "100%",
      marginBottom: 15
    }
  }
}));

const PlanCuentaList = () => {
  const {
    t
  } = useTranslation();

  const [openModal, setOpenModal] = useState(false); // search input

  const [searchValue, setSearchValue] = useState("");
  const [filteredItem, setFilteredItem] = useState([]);
  const [codigoReg, setCodigoReg] = useState("");

  const ApiProveedores = async () => {
    const { data } = await ObtenerPlanCuentaService();
    console.log(data.message)
    setFilteredItem(data.data)
  }

  const ApiCreateProveedores = async () => {
    const { data } = await CrearPlanCuentaService();
    console.log(data.data.codigo)
    setCodigoReg(data.data.codigo);

    setOpenModal(true);
  }
  //use context para actualizar el update y  delete
  const OpenModal = async (estado) => {
    if (estado) {
      await ApiCreateProveedores()
      await ApiProveedores()
      setOpenModal(estado);
    } else {
      await ApiProveedores()
      setOpenModal(estado);
    }
    await ApiProveedores()
  }
  useEffect(() => {
    ApiProveedores()
    const result = searchByName(plancuentaFake, searchValue);
    setFilteredItem(result);
  }, [searchValue]);
  return (
    <PlancuentaContext.Provider value={{
      modal:false,
      codigo:''
   }}>
      <Box pt={2} pb={4}>
        <HeadingWrapper justifyContent="space-between" alignItems="center">
          <FlexBox gap={0.5} alignItems="center">
            <IconWrapper>
              <ShoppingBasket sx={{
                color: "primary.main"
              }} />
            </IconWrapper>
            <H5>Plan cuentas</H5>
          </FlexBox>
          <SearchInput bordered={'true'} placeholder="Buscar proveedores" onChange={e => setSearchValue(e.target.value)} />
          <Button
            variant="contained"
            endIcon={<Add />}
            onClick={async () => { await OpenModal(true) }}>
            {t("Añadir Plan Cuenta")}
          </Button>
        </HeadingWrapper>
        <CustomTable columnShape={PlanCuentaColumns} data={filteredItem} />
        <CreatePlanCuentaModal open={openModal} onClose={() => OpenModal(false)} codigo={codigoReg} />
      </Box>
    </PlancuentaContext.Provider>
  );
};

export default PlanCuentaList;