import axios from "axios";

export async function CrearPlanCuentaService() {
    return await axios.get(`${process.env.REACT_APP_API}api/plan-cuenta/create`)
}
export async function ObtenerPlanCuentaService() {
    return await axios.get(`${process.env.REACT_APP_API}api/plan-cuenta`)
}
export async function GuardarPlanCuentaService({ codigoProveedor, nombreProveedor, dirrecion, credito, telefono }) {
    return await axios.post(`${process.env.REACT_APP_API}api/plan-cuenta`, { codigoProveedor, nombreProveedor, dirrecion, credito, telefono });
}
export async function ModificarPlanCuentaService({ id, codigoProveedor, nombreProveedor, dirrecion, credito, telefono }) {
    return await axios.put(`${process.env.REACT_APP_API}api/plan-cuenta/${id}`, { id, codigoProveedor, nombreProveedor, dirrecion, credito, telefono });
}
export async function EliminarPlanCuentaService(id) {
    return await axios.delete(`${process.env.REACT_APP_API}api/plan-cuenta/${id}`, {});
}